<template>
  <div>
    <!--  -->
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />

    <!--  -->
    <van-cell-group class="inputTop">
      <van-field
        v-model="username"
        required
        label=""
        left-icon="manager"
        placeholder=""
        label-class="left right"
        :error-message="error1 "
      />
      <van-field
        v-model="password"
        required
        label=""
        type="password"
        left-icon="lock"
        placeholder=""
        label-class="left right"
        :error-message="error2 "
      />
    </van-cell-group>

    <!--  -->
    <van-button type="danger" class="login-btn" @click="loginenter"></van-button>
    <van-button type="warning" class="login-btn" @click="newLogin"></van-button>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      username: '',
      password: '',
      error1: '',
      error2: ''
    }
  },
  methods: {
    onClickLeft () {
      // 
      this.$router.push('/aswer')
    },
    // 
    ...mapActions(['login']),
    loginenter () {
      // 
      if (!this.username) {
        this.error1 = ''
        return false
      } else {
        this.error1 = ''
      }
      if (!this.password) {
        this.error2 = ''
        return false
      } else {
        this.error2 = ''
      }
      // login
      this.login({
        username: this.username,
        password: this.password
      })
    },
    // 
    newLogin () {
      // 
      this.$router.push('/loginNew')
    }
  }
}
</script>

<style>
.inputTop {
  margin-top: 100px;
}
.left {
  padding-left: 25px;
}
.right {
  margin-right: -25px;
}

/*  */
.login-btn {
  margin-top: 50px;
  width: 70%;
  padding: 0 15px;
  font-size: 14px;
  margin-left: 15%;
}
</style>
